<template>
  <div class="container">
    <navbar></navbar>
    <div class="tip">
      请输入验证码
      <p>
        验证码已通过短信发送至 <span>{{ tel }}</span>
      </p>
    </div>
    <van-password-input
      ref="input"
      :value="value"
      :gutter="10"
      :mask="false"
      :focused="showKeyboard"
      @focus="showKeyboard = true"
    />
    <!-- 数字键盘 -->
    <van-number-keyboard
      v-model="value"
      :show="showKeyboard"
      @blur="handleBlur"
    />
    <p class="bottom_tip">没有收到验证码？<span>30秒后重发</span></p>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      showKeyboard: false,
      tel: "",
      value: "",
    };
  },
  watch: {
    value: {
      handler(newV) {
        if (newV.length == 6) {
          this.handleBlur(true);
        }
      },
      immediate: true,
      deep: true,
    },
  },
  mounted() {
    this.tel = this.$route.query.tel.replace(/(\d{3})\d{4}(\d{4})/, "$1****$2");
  },
  methods: {
    handleBlur(flag) {
      !flag && (this.showKeyboard = false);
      if (this.value.length < 6) {
        Toast("请补全验证码");
        return;
      }
      if (this.value.length == 6 && this.value != "123456") {
        Toast.fail("验证码错误");
        return;
      }
      if (this.value.length == 6 && this.value == "123456") {
        Toast.success("登录成功");
        setTimeout(() => {
          this.$router.push("/home");
        }, 1000);
      }
    },
  },
};
</script>

<style scoped>
.container {
  /* margin: 10px 20px; */
}
.tip {
  font-family: "PingFang SC";
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  height: 50px;
  margin-top: 20px;
}
.tip p {
  font-weight: 400;
  font-size: 12px;
  line-height: 22px;
  color: #999999;
  margin-top: 5px;
}
>>> .van-password-input {
  margin: 0;
  margin-top: 58px;
}
>>> .van-password-input__item {
  border-bottom: 1px solid #ddd;
  font-family: "DINOT";
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  color: #1d1d1f;
}
.bottom_tip {
  font-family: "PingFang SC";
  font-size: 12px;
  color: #999999;
  margin-top: 20px;
}
span {
  color: #4474fb;
}
</style>